import React, { useState, useEffect } from 'react';
import * as ChessJS from 'chess.js';
import { observer } from 'mobx-react-lite';

import { userStore } from '../../../mobx';
import { TemplateBoard } from '../../Board/TemplateBoard';
import { Button } from '../../Common/Button';

import styles from './MainMenu.module.scss';

const Chess = typeof ChessJS === 'function' ? ChessJS : ChessJS.Chess;

const MainMenu: React.FC = observer(() => {
  const { signInAsGuest, user } = userStore;

  const [stateChess, setStateChess] = useState<ChessJS.ChessInstance>();

  useEffect(() => {
    const chess = new Chess();
    setStateChess(chess);
  }, []);

  return (
    <div className={styles.wrapper}>
      <div className={styles['menu-wrapper']}>
        <div className={styles.menu}>
          {user.isAuth && (
            <React.Fragment>
              <Button
                classNameLink={styles.link}
                className={styles.button}
                href="/lobby-online"
                text=" "
                theme="primary"
              />
              <Button
                classNameLink={styles.link}
                className={styles.button}
                href="/lobby-offline"
                text=" "
                theme="primary"
              />
            </React.Fragment>
          )}
          {!user.isAuth && (
            <React.Fragment>
              <Button
                classNameLink={styles.link}
                className={styles.button}
                href="/sign-in"
                text=""
                theme="primary"
              />
              <Button
                classNameLink={styles.link}
                className={styles.button}
                href="/sign-up"
                text=""
                theme="primary"
              />

              <Button className={styles.button} text="  " onClick={signInAsGuest} theme="secondary" />
            </React.Fragment>
          )}
        </div>
      </div>
      {stateChess && <TemplateBoard stateChess={stateChess} isRandom myColor="w" />}
    </div>
  );
});

export { MainMenu };
